import { useState } from 'react';

export const useTheme = () => {
  let initTheme = 'dark';
  let localSettingTheme;
  if (typeof window !== 'undefined') {
    //    
    const isBrowserDarkMode = window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;

    initTheme = isBrowserDarkMode ? 'dark' : 'light';

    //        
    localSettingTheme = localStorage.getItem('theme');

    //            
    if (localSettingTheme) {
      initTheme = localSettingTheme;
    }
  }

  console.log('  Theme : ' + initTheme);
  const [theme, setTheme] = useState(initTheme);

  const setMode = (mode: string) => {
    //   localstorage        
    window.localStorage.setItem('theme', mode);
    console.log('  ' + mode);
    setTheme(mode);
  };

  const toggleTheme = () => setMode(theme === 'light' ? 'dark' : 'light');

  return { themeMode: theme, toggleTheme };
};
